<template>
  <div  class="big-table-wrap" ref="tableWrap">
    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">原发布流水号：</span>
            <Input v-model="oldPublishID" placeholder="原发布流水号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">发布流水号：</span>
            <Input v-model="publishID" placeholder="发布流水号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">消费者账号：</span>
            <Input v-model="userID" placeholder="消费者账号" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">换出积分：</span>
            <Input v-model="outPointsID" placeholder="换出积分" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">换入积分：</span>
            <Input v-model="inPointsID" placeholder="换入积分" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">委托状态：</span>
            <Select v-model="delegationStatue" class="input-width">
              <Option v-for="item in delegationStatueList" :value="item.child_value" :key="item.child_value" class="input-width">{{ item.child_name }}</Option>
            </Select>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">结束类型：</span>
            <Select v-model="endStatue" class="input-width">
              <Option v-for="item in endStatueList" :value="item.child_value" :key="item.child_value" class="input-width">{{ item.child_name }}</Option>
            </Select>
          </div>
        </Col>
        <Col span="10" v-show="showMore">
          <div  class="input-item">
            <daterange :name="'发布日期'" ref="regTime"  @start-time="timeChange('publishStart',$event)"  @end-time="timeChange('publishEnd',$event)"></daterange>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item">
            <Button type="primary" @click="reSearchInfo">查询</Button>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="resetSearch" >重置</Button>
          </div>
        </Col>
      </Row>
      <showbtn :sendNum="10" @show-hide="showMore=$event"></showbtn>
    </div>
    <!--操作按钮区域-->
    <div class="handle-wrap" ref="handleWrap">
      <div class="handle-btn-wrap">
        <Button type="primary" @click="exportInfo">导出</Button>
      </div>
    </div>
    <!--table表格区域-->
    <div class="table-wrap-style" ref="tableBox">
      <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" stripe border></Table>
    </div>
    <div class="page-wrap" ref="pageWrap">
      <div class="right">
        <Page :total="total" :current="page" @on-change="changePage" @on-page-size-change="changeSize" show-total show-sizer :page-size-opts="pageSizeOpts"></Page>
      </div>
    </div>
  </div>
</template>
<script>
  import { tableMethods } from "../../../js/mixintable";
  import daterange from '../../components/daterange'
  import showbtn from '../../components/showbtn'
  export default {
    name: "ExchangeEntrustQuery",//互换委托
    data () {
      return {
        /*搜索区域 条件*/
        oldPublishID:'', //发布流水号
        publishID:'', //发布流水号
        userID:'',//消费者账号
        outPointsID:'',//换出积分
        inPointsID:'',//换入积分
        delegationStatue:'',//委托状态
        endStatue:'',//结束类型
        publishStartTime:'',//分布开始时间
        publishEndTime:'',// 发布结束时间
        loading:false,
        delegationStatueList:[
          {
            child_name: '全部',
            child_value: ''
          }
        ],
        endStatueList:[
          {
            child_name: '全部',
            child_value: ''
          },
        ],
        /*table表格*/
        tableColumns: [
          {
            title: '原发布流水号',
            key: 'original_buy_seq',
            align: 'center',
            minWidth: 100,
          },
          {
            title: '发布流水号',
            key: 'serial_number',
            align: 'center',
            minWidth: 100,
          },
          {
            title: '消费者账号',
            align: 'center',
            key: 'user_no',
            minWidth: 150,
          },
          {
            title: '换出积分ID',
            align: 'center',
            key: 'swap_cv',
            minWidth: 100,
          },
          {
            title: '换出积分',
            align: 'center',
            key: 'swap_name',
            minWidth: 100,
          },
          {
            title: '换出数量',
            align: 'center',
            key: 'swap_amount',
            minWidth: 100,
          },
          {
            title: '预计折损数量',
            align: 'center',
            key: 'loss_amount',
            minWidth: 100,
          },
          {
            title: '折损比例值',
            align: 'center',
            key: 'loss_proportion',
            minWidth: 100,
          },
          {
            title: '折损固定值',
            align: 'center',
            key: 'loss_fixed',
            minWidth: 100,
          },

          {
            title: '换入积分ID',
            align: 'center',
            key: 'point_cv',
            minWidth: 100,
          },
          {
            title: '换入积分',
            align: 'center',
            key: 'point_name',
            minWidth: 100,
          },
          {
            title: '换入数量',
            align: 'center',
            key: 'point_amount',
            minWidth: 100,
          },
          {
            title: '中间积分ID',
            align: 'center',
            key: 'middle_id',
            minWidth: 100,
          },
          {
            title: '成交方式',//部分成交/全部成交
            align: 'center',
            key: 'deal_name',
            minWidth: 100,
          },
          {
            title: '发布时间',
            align: 'center',
            key: 'commit_time',
            minWidth: 100,
          },
          {
            title: '失效时间',
            align: 'center',
            key: 'final_time',
            minWidth: 100,
          },

          {
            title: '结束时间',
            key: 'end_time',
            align: 'center',
            minWidth: 100,
          },
          {
            title: '委托状态',//已委托/已成交/已撤单
            align: 'center',
            key: 'order_status',
            minWidth: 100,
          },
          {
            title: '结束类型',
            align: 'center',
            key: 'end_name',
            minWidth: 100,
          }
        ]
      }
    },
    components: {
      daterange,showbtn
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //获取委托状态
        this.getEntrustStateList()
        //获取委托状态
        this.getFinishTypeList()
        //查询数据
       // this.searchInfo();
        })
    },
    methods: {
      //查询数据
      searchInfo(){
        let that=this
        this.loading=true;
        let objData={
          original_buy_seq:this.oldPublishID,//发布流水号
          serial_number:this.publishID,//发布流水号
          user_no:this.userID,//消费者账号
          query_point:this.inPointsID,//换入积分
          query_swap:this.outPointsID,//换出积分
          order_status_cv:this.delegationStatue,//委托状态
          end_cv:this.endStatue,//结束类型
          min_commit_time:this.publishStartTime.length>0?this.publishStartTime.replace(/-/g,''):'',
          max_commit_time:this.publishEndTime.length>0?this.publishEndTime.replace(/-/g,''):'',
          pPage:this.page,
          pSize:this.size
        }
        let jsonData = protocol.getJsonParams(protocol.getExchangeEntrustQuery, objData);
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.$Loading.finish();
          let resultData=[]
          /*添加序号*/
          if(data.returnList&&data.returnList.length>0){
            data.returnList.forEach(function (item,index) {
              item.indexs=that.page*that.size+index-(that.size-1)
              resultData.push(item)
            })
          }
          this.tableData = resultData;
          this.total =data.total-0;
        })
      },
      //获取委托状态
      getEntrustStateList(){
        let jsonData = protocol.getJsonParams(protocol.getEntrustStateList, {});
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.delegationStatueList = this.delegationStatueList.concat( data.returnList);
        })
      },
      //获取结束状态
      getFinishTypeList(){
        let jsonData = protocol.getJsonParams(protocol.queryEndTypeList, {});
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.endStatueList = this.endStatueList.concat( data.returnList);
        })
      },
      //重置查询条件
      resetSearch(){
        /*搜索区域 条件*/
        this.oldPublishID=''// 账号
        this.publishID='' //唯一编码
        this.userID=''//状态
        this.inPointsID=''//邀请码
        this.outPointsID='' //邀请用户账号
        this.$refs.regTime.resetTime()
        this.delegationStatue='' //注册开始时间
        this.endStatue='' //注册结束时间
        this.publishStartTime='' //实名开始时间
        this.publishEndTime='' //实名结束时间
      },
      //获取时间变化并赋值
      timeChange(e,time){
        if(e=="publishStart"){
          this.publishStartTime=time
        }else if(e=="publishEnd"){
          this.publishEndTime=time
        }
      },
      exportOk(){
        let objData={
          original_buy_seq:this.oldPublishID,//发布流水号
          serial_number:this.publishID,//发布流水号
          user_no:this.userID,//消费者账号
          query_point:this.inPointsID,//换入积分
          query_swap:this.outPointsID,//换出积分
          order_status_cv:this.delegationStatue,//委托状态
          end_cv:this.endStatue,//结束类型
          min_commit_time:this.publishStartTime.length>0?this.publishStartTime.replace(/-/g,''):'',
          max_commit_time:this.publishEndTime.length>0?this.publishEndTime.replace(/-/g,''):'',
      }
        let jsonData = protocol.getJsonParams(protocol.exportExchangeEntrustQuery, objData);
        http.ajax('', jsonData, true, (data) => {
          window.location.href=loadUrl+data.excel_url
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import '../../../css/common.css';
</style>





